<template>
  <div class="bg-black" v-if="post">
    <!-- Hero Section -->
    <section class="relative pt-32 overflow-hidden">
      <img
        :src="post.heroImage"
        :alt="post.title"
        class="absolute inset-0 w-full h-full object-cover opacity-20 z-0"
      />
      <div
        class="absolute inset-0 bg-gradient-to-b from-black/60 via-black/90 to-black z-0"
      ></div>

      <div class="container mx-auto px-4 relative z-10 max-w-[1200px]">
        <NuxtLink
          to="/blog"
          class="inline-flex items-center gap-2 text-white/70 hover:text-[#FEE337] transition-colors mb-8 group"
        >
          <svg
            class="w-5 h-5 group-hover:-translate-x-1 transition-transform"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M15 19l-7-7 7-7"
            />
          </svg>
          Back to Blog
        </NuxtLink>

        <div class="flex items-center gap-3 mb-6 text-sm">
          <span
            class="px-3 py-1 bg-[#FEE337] text-black text-xs font-bold rounded-full uppercase"
            >{{ post.category }}</span
          >
          <span class="text-white/60">{{ post.date }}</span>
          <span class="text-white/60">•</span>
          <span class="text-white/60">{{ post.readTime }}</span>
        </div>

        <h1
          class="text-4xl md:text-6xl font-display font-bold text-white mb-6 leading-tight"
        >
          {{ post.title }}
        </h1>

        <p class="text-xl text-white/70 mb-8 leading-relaxed">
          {{ post.excerpt }}
        </p>

        <!-- Author Info -->
        <div class="flex items-center gap-4">
          <div
            class="w-12 h-12 rounded-full bg-gradient-to-br from-[#FEE337] to-[#FEE337]/60 flex items-center justify-center text-black font-bold text-lg"
          >
            {{ post.author.initials }}
          </div>
          <div>
            <p class="text-white font-bold">{{ post.author.name }}</p>
            <p class="text-white/60 text-sm">{{ post.author.role }}</p>
          </div>
        </div>
      </div>
    </section>

    <!-- Article Content -->
    <article class="py-16 bg-black">
      <div class="container mx-auto px-4 max-w-[1200px]">
        <!-- Featured Image -->
        <div
          class="mb-12 rounded-2xl overflow-hidden border border-white/10"
          data-aos="zoom-in"
        >
          <img
            :src="post.featuredImage"
            :alt="post.title"
            class="w-full h-auto"
          />
        </div>

        <!-- Article Body (Dynamic Content) -->
        <div class="prose prose-invert prose-lg max-w-[1200px]">
          <!-- Loop through content sections -->
          <div
            v-for="(section, index) in post.content"
            :key="index"
            :data-aos="'fade-up'"
            :data-aos-delay="index * 50"
          >
            <!-- Paragraph -->
            <p
              v-if="section.type === 'paragraph'"
              class="text-white/80 text-lg leading-relaxed mb-6"
            >
              {{ section.text }}
            </p>

            <!-- Heading -->
            <h2
              v-if="section.type === 'heading'"
              class="text-3xl font-display font-bold text-white mt-12 mb-6"
            >
              {{ section.text }}
            </h2>

            <!-- List -->
            <ul
              v-if="section.type === 'list'"
              class="space-y-3 mb-8 text-white/80"
            >
              <li
                v-for="(item, i) in section.items"
                :key="i"
                class="flex items-start gap-3"
              >
                <span class="text-[#FEE337] mt-1">•</span>
                <span>{{ item }}</span>
              </li>
            </ul>

            <!-- Callout Box -->
            <div
              v-if="section.type === 'callout'"
              class="p-8 bg-white/5 border border-[#FEE337]/20 rounded-xl my-8"
            >
              <h3 class="text-2xl font-bold text-[#FEE337] mb-4">
                {{ section.title }}
              </h3>
              <p class="text-white/80 text-lg leading-relaxed">
                {{ section.text }}
              </p>
            </div>

            <!-- Image -->
            <div
              v-if="section.type === 'image'"
              class="my-10 rounded-xl overflow-hidden border border-white/10"
            >
              <img
                :src="section.url"
                :alt="section.caption"
                class="w-full h-auto"
              />
              <p
                v-if="section.caption"
                class="text-center text-white/50 text-sm mt-2"
              >
                {{ section.caption }}
              </p>
            </div>
          </div>

          <!-- CTA Box -->
          <div
            class="p-8 bg-gradient-to-br from-[#FEE337]/10 to-transparent border border-[#FEE337]/30 rounded-2xl my-12"
            data-aos="zoom-in"
          >
            <h3 class="text-2xl font-bold text-white mb-4">
              Ready to experience AI-powered research?
            </h3>
            <p class="text-white/70 mb-6">
              Join thousands of Indian investors who've upgraded their research
              workflow with Yuktrix. Start free today.
            </p>
            <NuxtLink
              to="/"
              class="inline-block bg-[#FEE337] text-black px-8 py-4 rounded-full font-bold hover:bg-white transition-all"
            >
              Try Yuktrix Free
            </NuxtLink>
          </div>
        </div>
      </div>
    </article>

    <!-- Related Posts -->
    <section class="py-20 bg-black border-t border-white/10">
      <div class="container mx-auto px-4">
        <h2
          class="text-3xl font-display font-bold text-white mb-12 text-center"
          data-aos="fade-up"
        >
          Continue Reading
        </h2>
        <div
          class="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-[1200px] mx-auto"
        >
          <article
            v-for="relatedPost in relatedPosts"
            :key="relatedPost.slug"
            class="bg-white/5 rounded-2xl border border-white/10 overflow-hidden hover:border-[#FEE337]/30 transition-all group"
            data-aos="fade-up"
          >
            <div class="relative h-48 overflow-hidden">
              <img
                :src="relatedPost.image"
                :alt="relatedPost.title"
                class="w-full h-full object-cover group-hover:scale-105 transition-transform duration-700"
              />
            </div>
            <div class="p-6">
              <h3
                class="text-lg font-bold text-white mb-2 group-hover:text-[#FEE337] transition-colors line-clamp-2"
              >
                {{ relatedPost.title }}
              </h3>
              <p class="text-white/60 text-sm mb-4 line-clamp-2">
                {{ relatedPost.excerpt }}
              </p>
              <NuxtLink
                :to="`/blog/${relatedPost.slug}`"
                class="inline-flex items-center gap-2 text-[#FEE337] text-sm font-bold"
              >
                Read More
                <svg
                  class="w-4 h-4"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M17 8l4 4m0 0l-4 4m4-4H3"
                  />
                </svg>
              </NuxtLink>
            </div>
          </article>
        </div>
      </div>
    </section>

    <!-- 404 State -->
  </div>
  <div v-else class="min-h-screen bg-black flex items-center justify-center">
    <div class="text-center">
      <h1 class="text-4xl font-bold text-white mb-4">Post Not Found</h1>
      <NuxtLink to="/blog" class="text-[#FEE337] hover:underline"
        >Back to Blog</NuxtLink
      >
    </div>
  </div>
</template>
<script setup>
import { computed } from "vue";
import { useRoute } from "vue-router";

const route = useRoute();
const slug = route.params.slug;

const { getPostBySlug, getAllPosts } = useBlogPosts();

// Get the current post
const post = computed(() => getPostBySlug(slug));

// Get related posts (exclude current, limit to 3)
const relatedPosts = computed(() => {
  return getAllPosts()
    .filter((p) => p.slug !== slug)
    .slice(0, 3);
});
</script>
